:root {
  --c-bg: #000;
  --c-text: #f2f2f2;
  --fs-text: 14px;
  --fs-heading: 18px;
  --fs-title: 60px;
  --column: 90px;
  --margin: 80px;
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  width: 100vw;
  color: var(--c-text);
  font-family: Cinzel, serif;
  font-weight: 400;
  font-size: var(--fs-text);
  font-feature-settings: "liga";
  text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  background: url("bg-projects.fd533cd0.png") center / cover no-repeat fixed;
  display: block;
}

div {
  display: block;
}

.pageHeader {
  z-index: 2;
  width: calc(var(--column) * 9);
  height: calc(var(--fs-title)  + .8rem);
  padding-top: 50px;
  padding-right: var(--margin);
  box-sizing: content-box;
  border-bottom: 1px solid;
  justify-content: space-between;
  align-items: baseline;
  column-gap: 1rem;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

.pageHeader .title {
  align-items: baseline;
  column-gap: 1rem;
  display: flex;
}

.pageHeader .title .label {
  font-size: var(--fs-heading);
}

.pageHeader .title .name {
  font-size: var(--fs-title);
}

.siteHeader {
  z-index: 2;
  position: fixed;
  top: 40px;
  left: 40px;
}

.siteHeader_title {
  letter-spacing: -.02em;
  cursor: crosshair;
  font-family: Cinzel, serif;
  font-size: 30px;
  line-height: .9;
}

.siteHeader_title a {
  color: var(--c-text);
  cursor: crosshair;
  text-decoration: none;
}

.siteHeader_title .line3 {
  text-align: right;
}

.projects_inner {
  z-index: 1;
  position: absolute;
  top: 13rem;
  right: 15rem;
}

.projectItem {
  position: relative;
}

.projectItem:hover {
  color: #8080809d;
  transition: all .5s;
}

.image_one {
  z-index: 2;
  width: calc(var(--column) * 5);
  visibility: hidden;
  border: 1px solid #fff;
  position: absolute;
  top: 150px;
  left: -600px;
}

.image_two {
  z-index: 2;
