  position: absolute;
  top: -230px;
  left: -600px;
}

.projectItem:hover .image_one, .projectItem:hover .image_two, .projectItem:hover .image_three, .projectItem:hover .image_four, .projectItem:hover .image_five {
  visibility: visible;
}

a {
  color: currentColor;
  cursor: crosshair;
  padding: 2rem;
  text-decoration: none;
}

h2 {
  font-size: 30px;
}

.cat {
