}

.one .index, .two .index, .three .index, .four .index, .five .index {
  font-family: Cinzel, serif;
  font-size: 18px;
  font-weight: 600;
  position: absolute;
  top: 40px;
  left: -36px;
}

.footerCopyright {
  padding-bottom: 3.5rem;
  position: absolute;
  bottom: -300px;
  left: 430px;
}

.footerCopyright .en {
  letter-spacing: -.04rem;
  opacity: .5;
  font-family: Marcellus, serif;
